import server from '../../server.js';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			name:[],
			sex:'♀',
			TabCur: 0,
			CustomBar: this.CustomBar,
			page:1,
			weikong:true,
			postList:[],
			status: 'loading',
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			}
		}
	},
	onLoad:function(e){
		console.log(e.auid)
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/user.php?type=getUserInfo', //仅为示例，并非真实接口地址。
			data: {
				auid: e.auid
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				console.log(res)
				this.name=res.data.data
				this.getPostList(this.name.Auid)
				
			}
		});
		if(this.name.Gender=="2"){
			this.sex='♀'
		}else{
			this.sex='♂'
		}
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e
		},
		getPost:function(id){
			console.log(id)
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		},
		getEditInfo:function(){
			uni.navigateTo({
				url: '../editinfo/editinfo'
			});
		},
		getPostList(auid){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=myPostList', //仅为示例，并非真实接口地址。
				data: {
					uid: auid,
					count:10,
					page:this.page
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.data.length==undefined){
						this.status ="noMore";
						this.weikong=true;
					}else{
						this.postList=res.data.data;
						this.status ="more";
						this.weikong=false;
						console.log(this.postList);
					}
					
					
				}
			}); 
		}
	},
	onReachBottom:function(){
		this.status ="loading";
		this.page++;
		console.log("拉倒低了",this.page)
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=myPostList', //仅为示例，并非真实接口地址。
			data: {
				uid: this.name.Auid,
				count:10,
				page:this.page
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				if(res.data.data.length==undefined){
					this.page--;
					this.status ="noMore";
				}else{
					this.postList=this.postList.concat(res.data.data);
					this.status ="more";
					console.log(this.postList);
				}
				
			}
		});
	}
}
